.homeChief{
  padding: 0px 0 70px;

  &.with-background{
    padding: 40px 0 70px;
    @media screen and (min-width: 830px) {
      padding: 40px 0 90px;
    }

    @media screen and (min-width: 1152px) {
      padding: 40px 0 120px;
    }
  }

  @media screen and (min-width: 830px) {
    padding: 0px 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0px 0 120px;
  }

  .container{
    position: relative;
  }

  &-figure{
    position: absolute;
    bottom: -105px;
    right: -30px;
    width: 167px;
    height: 185px;

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
      bottom: auto;
      top: calc(100% - 10px);
      right: -25px;
      width: 214px;
      height: 231px;
    }

    @media screen and (min-width: 640px) {
      width: 260px;
      height: 311px;
    }

    @media screen and (min-width: 1290px) {
      width: 435px;
      height: 409px;
      right: -40px;
      top: 100%;
    }

  }

  &.with-background::before{
    content: "";
    display: block;
    position: absolute;
    top: 0px;
    left: 0;
    right: 0;
    transform: translateY(-100%);
    width: 100%;
    padding-top: 54px / 320px * 100%;
    background-repeat: no-repeat;
    background-size: 100%;
    background-image: url("data:image/svg+xml,%3Csvg width='320' height='54' viewBox='0 0 320 54' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M320 0C225.5 44.5 102.5 41.5 0 0V54H320V0Z' fill='%23F6F9FC'/%3E%3C/svg%3E%0A");

    @media screen and (min-width: 480px){
      padding-top: 70px / 480px * 100%;
      background-image: url("data:image/svg+xml,%3Csvg width='480' height='70' viewBox='0 0 480 70' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M480 0C326.094 51.4943 159.224 50.6897 0 0V70H480V0Z' fill='%23F6F9FC'/%3E%3C/svg%3E%0A");
    }

    @media screen and (min-width: 640px){
      padding-top: 87px / 640px * 100%;
      background-image: url("data:image/svg+xml,%3Csvg width='636' height='87' viewBox='0 0 636 87' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M636 0C430.792 64 208.299 63 -4 0V87H636V0Z' fill='%23F6F9FC'/%3E%3C/svg%3E%0A");
    }

    @media screen and (min-width: 830px){
      padding-top: 99px / 960px * 100%;
      background-image: url("data:image/svg+xml,%3Csvg width='960' height='99' viewBox='0 0 960 99' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M960 0C652.188 72.8276 318.449 71.6897 0 0V99H960V0Z' fill='%23F6F9FC'/%3E%3C/svg%3E%0A");
    }

    @media screen and (min-width: 1152px){
      padding-top: 157px / 1920px * 100%;
      background-image: url("data:image/svg+xml,%3Csvg width='1920' height='157' viewBox='0 0 1920 157' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M1920 0C1285.2 121.29 645.13 127.72 0 0V157H1920V0Z' fill='%23F6F9FC'/%3E%3C/svg%3E%0A");
    }

  }

  &-title{
    margin-bottom: 70px;
  }

  &-image{
    margin: 0 auto;
    max-width: 674px;
    img{
      width: 100%;
      height: auto;
    }
  }

  &-advantages{
    position: relative;
    margin: 10px auto 0;
    z-index: 2;
    max-width: 480px;
    display: flex;
    flex-direction: column;
    &__title{
      margin-bottom: 15px;
      font-weight: 700;
      font-size: 19px;
      line-height: 130%;
      color: #262626;

      .blue{
        font-size: 26px;
        margin: 4px 0;
      }

      span{
        display: block;
      }

    }
    &__items{
      display: flex;
      flex-direction: column;
      margin-bottom: 15px;
      .item{
        margin: 7px 0;
        display: flex;
        align-items: center;
        background: #FFFFFF;
        box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
        border-radius: 10px;
        padding: 12px 10px;

        &-icon{
          flex-shrink: 0;
          margin-right: 16px;
          width: 34px;
          height: 34px;
          display: flex;
          align-items: center;
          justify-content: center;
        }
        &-text{
          font-size: 13px;
          line-height: 150%;
          letter-spacing: 0.05em;
          color: #262626;
        }
      }
    }
    &__btn{
      margin: 0 auto;
      padding: 16px 18px;
    }
  }

  &-perforation{
    position: absolute;
    bottom: 6px;
    right: 20px;
    z-index: 1;
  }

  @media screen and (min-width: 480px) {
    &-advantages{
      &__title{
        font-size: 25px;

        .section-h3{
          font-size: 30px;
        }

      }


    }
    
  }

  @media screen and (min-width: 640px) {
    &-advantages{
      &__title{
        .section-h3{
          font-size: 38px;
        }
        font-size: 28px;
      }

      .item{
        padding: 16px 24px;
      }

      .item-text{
        font-size: 16px;
      }

      .item-icon{
        width: 40px;
        height: 40px;

        svg{
          width: 100%;
          height: 100%;
          object-fit: contain;
        }

      }

      &__btn{
        font-size: 16px;
        line-height: 21px;
        padding: 20px 27px;
      }
    }
  }

  @media screen and (min-width: 960px) {
    .container{
      display: flex;
      flex-wrap: wrap;
    }

    &-title{
      width: 100%;

    }

    &-image{
      width:674px / 1160px * 100%;
      margin: 0 auto 0 0;
    }

    &-advantages{
      margin-left: -104px;
      margin-right: 0;
      width: 100%;
    }

  }

  @media screen and (min-width: 1152px) {

    &-title{
      text-align: center;
    }

    &-advantages{
      max-width: 597px;
      margin-left: -148px;
      &__title{
        font-size: 35px;

        .section-h3{
          font-size: 45px;
        }

      }

      &__items{
        margin-bottom: 40px;
      }

      .item{
        margin: 10px 0;
      }

      .item-text{
        font-size: 20px;
      }

      .item-icon{
        width: 45px;
        height: 45px;
      }

      &__btn{
        padding: 23px;
        font-size: 20px;
        line-height: 27px;
      }
    }
  }



}