.faq{
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }
  .container{
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .container{
    position: relative;
  }

  &-figure{
    position: absolute;
    top: 100%;
    left: -30px;
    z-index: -1;
    width:203px;
    height: 192px;
    transform: translateY(-95%);

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
      width: 255px;
      height: 242px;
      left: -25px;
    }

    @media screen and (min-width: 640px) {
      width: 368px;
      height: 361px;
      left: -40px;
      transform: translateY(-56%);
    }

    @media screen and (min-width: 1152px) {
      width: 415px;
      height: 350px;
      left: -160px;
      transform: translateY(-65%);
    }

  }

  .section-h2{
    margin-bottom: 20px;
    width: 100%;
  }
  &-items{
    width: 100%;
    display: flex;
    flex-direction: column;

    &__item{
      margin: 25px 0;
      background: #FFFFFF;
      box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      padding: 0 10px 20px;

      .head{
        display: flex;
        align-items: flex-start;
        transform: translateY(-20px);
        margin-left: -15px;

        &-image{
          width: 80px;
          height: 80px;
          padding: 8px;
          background: rgba(140, 82, 255, 0.3);
          border-radius: 60px;
          flex-shrink: 0;
          margin-right: 10px;
          display: flex;
          align-items: center;
          justify-content: center;
          img{
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }

        &-question{
          margin-top: 10px;
          font-weight: 700;
          font-size: 12px;
          line-height: 150%;
          letter-spacing: 0.05em;
          color: #FFFFFF;
          padding: 10px 10px 13px 10px;
          background: #8C52FF;
          border-radius: 10px;
          max-width: calc(100% - 90px);
        }
      }

      .answer{
        font-size: 12px;
        line-height: 150%;
        letter-spacing: 0.05em;
      }

    }
  }
  &-btns{
    margin-top: 15px;
    display: flex;
    flex-direction: column;
    .btn{
      margin: 5px 0;
      min-width: 220px;
      padding: 15px 27px;
    }
  }

  @media screen and (min-width: 576px) {
    &-btns{
      flex-direction: row;
      justify-content: space-between;
      width: calc(100% + 10px);
      margin: 0 -5px;
      .btn{
        max-width: 260px;
        flex-basis: 260px;
        min-width: 0;
        margin: 0 5px;
      }
    }
  }

  @media screen and (min-width: 640px) {

    &-btns{
      margin-top: 20px;

      .btn{
        font-size: 16px;
        line-height: 21px;
        padding: 20px 36px;
      }

    }

    &-items__item .head{
      align-items: center;
    }

    &-items__item{
      padding: 0 20px 20px;
    }

    &-items__item .head-image{
      width: 100px;
      height: 100px;

      img{
        width: 80px;
        height: 80px;
      }

    }

    &-items__item .head-question{
      font-size: 16px;
    }
    &-items__item .answer{
      font-size: 16px;
    }
  }

  @media screen and (min-width: 1152px) {

    &-items{
      &__item {
        margin: 35px 0;
        .head{
          margin-top: -35px;
        }
        .head-question{
          font-size: 20px;
          line-height: 150%;
        }
        .head-image{
          width: 120px;
          height: 120px;
        }
        .head-image img{
          width: 100px;
          height: 100px;
        }
        .answer{
          font-size: 20px;
          line-height: 150%;
        }
      }
    }

    &-btns{
      justify-content: center;
      .btn{
        margin: 0 20px;
        font-size: 20px;
        line-height: 27px;
        padding: 20px 33px;
        min-width: 300px;
        max-width: none;
      }
    }
  }

}