.homeContacts{
  padding: 0;

  &-items{
    display: flex;
    flex-wrap: wrap;
    &__item{
      background: #FFFFFF;
      box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      padding: 20px;
      margin: 7px 0;
      width: 100%;
    }

    &__name{
      display: flex;
      align-items: center;
      margin-bottom: 16px;
      font-size: 16px;
      line-height: 21px;


      @media screen and (min-width: 1152px) {
        margin-bottom: 19px;
      }

      .icon{
        flex-shrink: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 15px;
        width: 30px;
        height: 30px;

        svg{
          width: 100%;
          height: 100%;
          object-fit: contain;
        }

        @media screen and (min-width: 1152px) {
          width: 38px;
          height: 38px;
        }

      }

      @media screen and (min-width: 1152px) {
        font-size: 20px;
        line-height: 27px;
      }

    }

    &__value{
      font-weight: 400;
      font-size: 13px;
      line-height: 130%;
      a{
        font-weight: 400;
        font-size: 13px;
        line-height: 130%;
      }

      @media screen and (min-width: 640px) {
        font-size: 14px;
        line-height: 130%;
        a{
          font-size: 14px;
          line-height: 130%;
        }
      }

      @media screen and (min-width: 1152px) {
        font-size: 17px;
        line-height: 130%;
        a{
          font-size: 17px;
          line-height: 130%;
        }
      }

    }

  }

  &-contacts{
    &__block{
      margin-top: 7px;
      margin-bottom: -30px;
      padding: 20px;
      background: #FFFFFF;
      box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      font-size: 13px;
      line-height: 130%;
      color: #262626;
      position: relative;
      z-index: 1;

      .section-h3{
        margin-bottom: 20px;
        font-size: 25px;
        line-height: 150%;

        @media screen and (min-width: 480px) {
          font-size: 28px;
          line-height: 150%;
        }

        @media screen and (min-width: 640px) {
          font-size: 30px;
          line-height: 150%;
        }

        @media screen and (min-width: 1152px) {
          font-size: 38px;
          line-height: 150%;
        }

      }

      .item{
        display: flex;
        align-items: center;
        margin: 7px 0;
        &.phone{
          a{
            font-weight: 700;
            font-size: 20px;
            line-height: 27px;
            color: #262626;
          }
        }
        svg{
          margin-right: 15px;
          flex-shrink: 0;
        }

        @media screen and (min-width: 640px) {
          font-size: 16px;
          line-height: 21px;

          &.phone{
            a{
              font-size: 22px;
              line-height: 29px;
            }
          }

        }

        @media screen and (min-width: 1152px) {
          font-size: 20px;
          line-height: 27px;

          &.phone{
            a{
              font-size: 25px;
              line-height: 33px;
            }
          }

        }

      }

      .btn{
        margin-top: 13px;
        padding: 12px 31px;

        @media screen and (min-width: 640px) {
          font-size: 16px;
          line-height: 21px;
          padding: 10px 24px;
        }

        @media screen and (min-width: 1152px) {
          font-size: 20px;
          line-height: 27px;
          padding: 15px 37px;
        }

      }

    }
    &__map{
      position: relative;
      left: 50%;
      width: 100vw;
      margin-left: -50vw;
      padding-top: 500px / 320px * 100%;

      @media screen and (min-width: 480px) {
        padding-top: 600px / 480px * 100%;
      }

      @media screen and (min-width: 640px) {
        padding-top: 650px;
      }

      @media screen and (min-width: 960px) {
        padding-top: 600px;
      }


      #map{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        pointer-events: none;
      }

    }
  }

  @media screen and (min-width: 480px) {
    .container{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    &-items{
      margin: 0 -10px;
      width: calc(100% + 20px);
      max-width: 500px;

      &__item{
        width: calc(50% - 20px);
        margin: 10px;
      }

    }
    
    &-contacts{
        max-width: 480px;
        width: 100%;
    }

  }

  @media screen and (min-width: 960px) {
    &-items{
      width: calc(100% + 20px);
      max-width: calc(100% + 20px);
      margin-bottom: 30px;

      &__item{
        width: calc(25% - 20px);
        margin: 10px;
      }

    }

    &-contacts{
      max-width: 100%;
      width: 100%;
      position: relative;
      &__block{
        position: absolute;
        top: 20px;
        left: 0;
        max-width: 450px;
        width: 100%;
      }
    }

  }

  @media screen and (min-width: 1152px) {
    &-contacts{
      &__block{
        padding: 25px;
        top: 80px;
      }
    }

  }

}