.detailPlastic {
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .container{
    position: relative;

    & > div:last-child{
      margin-bottom: 0;
    }

  }

  &-figure{
    position: absolute;
    top: -30px;
    left: -30px;
    z-index: -1;
    width:206px;
    height: 212px;
    transform: translateY(-40%);

    &.bottom{
      top: auto;
      left: auto;
      transform: none;
      bottom: -30px;
      right: -30px;
    }

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
      width: 259px;
      height: 280px;
    }

    @media screen and (min-width: 640px) {
      width: 310px;
      height: 346px;
      top: -15px;
    }

    @media screen and (min-width: 1152px) {
      width: 452px;
      height: 371px;
      top: 0;
      transform: none;
      left: -160px;

      &.bottom{
        bottom: -30px;
        right: 25px;
        transform: translateX(50%);
      }

    }

  }

  .period-block{
    @media screen and (min-width: 640px) {
      padding: 20px 10px 20px 30px;

      &__caption {
        font-size: 16px;
        margin-top: 0;
        margin-left: -40px;
      }

      &__text {
        font-size: 16px;
      }
    }
    @media screen and (min-width: 1152px) {
      &__caption {
        font-size: 20px;
        margin-right: 0;
        margin-left: -50px;
        padding: 13px 30px;
      }

      &__text {
        font-size: 20px;
      }

    }
  }

  .recommend-block{
    @media screen and (min-width: 640px) {
      padding: 20px 20px 28px;
      &__caption {
        font-size: 16px;
        margin-top: 0;
        margin-right: -30px;
      }

      &__title {
        font-size: 23px;
      }

      ul.list li,
      ul.list li a{
        font-size: 16px;
        line-height: 150%;
      }

    }
    @media screen and (min-width: 1152px) {
      padding: 20px 30px 30px;
      &__caption {
        font-size: 20px;
        margin-right: -44px;
        padding: 13px 30px;
      }

      &__title {
        font-size: 25px;
      }

      ul.list li,
      ul.list li a{
        font-size: 20px;
      }

      ul.list li{
        padding-left: 40px;
      }

      ul.list li::before{
        width: 24px;
        height: 24px;

      }

    }
  }


}