.detailSteps {
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .section-h2 {
    margin-bottom: 40px;
  }

  .container{
    position: relative;
  }

  &-figure{
    position: absolute;
    top: 100%;
    right: -30px;
    z-index: -1;
    width:218px;
    height: 205px;
    transform: translateY(-65%);

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    &.top{
      top: 10%;
    }

    @media screen and (min-width: 480px) {
      width: 274px;
      height: 258px;
      right: -35px;
      transform: translateY(-60%);
    }

    @media screen and (min-width: 640px) {
      width: 327px;
      height: 308px;
      transform: translateY(-50%);
    }

    @media screen and (min-width: 1152px) {
      width: 408px;
      height: 366px;
      top: 100%;
      transform: none;
      right: auto;
      left: -130px;

      &.top{
        top: 0;
      }

    }

  }

  .steps-items {
    counter-reset: section;

    &__item {
      background: #FFFFFF;
      box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      padding: 15px 10px;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      margin-bottom: 50px;
      position: relative;

      &::before {
        position: absolute;
        top: 10px;
        right: 10px;
        display: block;
        counter-increment: section;
        content: counter(section);
        font-weight: 700;
        font-size: 13px;
        line-height: 150%;
        letter-spacing: 0.05em;
        color: #8C52FF;
      }

      &::after {
        content: "";
        display: block;
        width: 18px;
        height: 18px;
        position: absolute;
        left: 0;
        right: 0;
        margin: 0 auto;
        bottom: -19px;
        transform: translateY(100%);
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url("data:image/svg+xml,%3Csvg width='19' height='17' viewBox='0 0 19 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17.0162 6.5864C17.4062 6.1964 18.0462 6.2064 18.4262 6.6064C18.8062 6.9964 18.7962 7.6264 18.4062 8.0164L10.0462 16.1664C9.66622 16.5464 9.04622 16.5464 8.65622 16.1664L0.306219 8.0164C-0.0937805 7.6264 -0.103781 6.9964 0.286219 6.6064C0.666218 6.2064 1.29622 6.1964 1.69622 6.5864L9.35622 14.0564L17.0162 6.5864ZM17.0162 0.326396C17.4062 -0.0636041 18.0462 -0.0536042 18.4262 0.346396C18.8062 0.736396 18.7962 1.3664 18.4062 1.7564L10.0462 9.9064C9.66622 10.2864 9.04622 10.2864 8.65622 9.9064L0.306219 1.7564C-0.0937805 1.3664 -0.103781 0.736396 0.286219 0.346396C0.666218 -0.0536042 1.29622 -0.0636041 1.69622 0.326396L9.35622 7.7964L17.0162 0.326396Z' fill='%238C52FF'/%3E%3C/svg%3E%0A");

      }

      &:last-child {
        margin-bottom: 0;

        &::after {
          display: none;
        }
      }

      .icon {
        width: 52px;
        height: 52px;
        margin-top: -20px;
        flex-shrink: 0;
        position: absolute;
        top: 0;
        left: 10px;

        svg {
          width: 100%;
          height: auto;
        }
      }

      .name {
        padding-left: 67px;
        font-weight: 700;
        font-size: 15px;
        line-height: 130%;
        letter-spacing: 0.05em;
        padding-right: 10px;
      }

      .text {
        margin-top: 10px;
        font-size: 13px;
        line-height: 150%;
        letter-spacing: 0.05em;
      }

    }
  }

  .steps-caption {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .btn{
      padding: 14px;
      font-size: 13px;
      line-height: 17px;

      @media screen and (min-width: 480px) {
        padding: 15px;
      }

      @media screen and (min-width: 640px) {
        font-size: 16px;
        line-height: 21px;
        padding: 20px 17px;
      }

      @media screen and (min-width: 960px) {
        padding: 20px 27px;
      }

      @media screen and (min-width: 1152px) {
        font-size: 20px;
        line-height: 27px;
        padding: 23px 27px;
      }
}
    &__text {
      text-align: center;
      font-size: 13px;
      line-height: 150%;
      text-align: center;
      letter-spacing: 0.05em;
      margin-bottom: 30px;
      max-width: 782px;
    }

    @media screen and (min-width: 640px) {
      &__text {
        font-size: 16px;
        margin-bottom: 40px;
      }
    }

    @media screen and (min-width: 1152px) {
      margin-top: 40px;
      &__text {
        font-size: 20px;
      }
    }

  }

  @media screen and (min-width: 640px) {

    .steps-items {
      &__item {
        padding: 20px 30px;
        margin-bottom: 55px;

        &::before {
          font-size: 20px;
          top: 10px;
          right: 20px;
        }

        .icon {
          width: 80px;
          height: 80px;
          margin-top: -30px;
          left: 20px;
        }

        .name {
          font-size: 23px;
          padding-left: 87px;
        }

        .text {
          font-size: 16px;
        }

      }
    }

  }

  @media screen and (min-width: 830px) {

    .steps-items {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -10px;
      width: calc(100% + 20px);
      justify-content: center;

      &__item {
        width: calc(50% - 20px);
        margin: 30px 10px;
        max-width: 640px;
        flex-grow: 1;

        &.fullwidth {
          width: calc(100% - 20px);
          max-width: 1030px;

          &::after {
            top: auto;
            left: 0;
            right: 0;
            margin: 0 auto;
            bottom: -20px;
            transform: translateY(100%);
            display: block !important;
          }

          &:last-child {
            &::after {
              display: none !important;
            }
          }

        }

        &:nth-child(even) {
          &::after {
            display: none;
          }
        }

        &::after {
          right: -20px;
          left: auto;
          top: 50%;
          transform: translateY(-50%) rotate(-90deg);
        }
      }
    }

  }

  @media screen and (min-width: 1152px) {

    .steps-items {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -20px;
      width: calc(100% + 40px);
      justify-content: center;

      &__item {
        width: calc(50% - 40px);
        margin: 35px 20px;
        max-width: 700px;
        flex-grow: 1;

        &.fullwidth {
          width: calc(100% - 20px);
          max-width: 1030px;

          &::after {
            top: auto;
            left: 0;
            right: 0;
            margin: 0 auto;
            bottom: -19px;
            transform: translateY(100%);
          }

        }

        &:nth-child(even) {
          &::after {
            display: block;
          }
        }

        &::after {
          right: -32px;
          left: auto;
          top: 50%;
          transform: translateY(-50%) rotate(-90deg);
        }

        .name {
          font-size: 25px;
        }

        .text {
          font-size: 20px;
        }

      }
    }

  }

}