.detailAgainst {
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .container{
    position: relative;
  }

  &-figure{
    position: absolute;
    top: 100%;
    left: -15px;
    z-index: -1;
    width:198px;
    height: 195px;
    transform: translateY(-50%);

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
      width: 248px;
      height: 245px;
    }

    @media screen and (min-width: 640px) {
      width: 297px;
      height: 327px;
      top: 0;
      left: -25px;
      transform: translateY(-81px);
    }

    @media screen and (min-width: 1152px) {
      width: 372px;
      height: 349px;
      top: 100%;
      left: -130px;
      transform: translateY(-50%);
    }

  }

  &-image {

    img {
      width: 100%;
      height: auto;
    }
  }

  &-text {
    margin: 0 10px;
    margin-top: -10px;
    position: relative;
    z-index: 1;

    &__forbidden {
      margin-top: 20px !important;
    }

    li::before{
      background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M1.5 8.36L8.36 1.5C10.36 -0.5 13.64 -0.5 15.64 1.5L22.5 8.36C24.5 10.36 24.5 13.64 22.5 15.64L15.64 22.5C13.64 24.5 10.36 24.5 8.36 22.5L1.5 15.64C-0.5 13.64 -0.5 10.36 1.5 8.36V8.36Z' fill='%238C52FF'/%3E%3Cpath d='M8.96055 16.4596C8.57055 16.8496 7.93055 16.8496 7.54055 16.4596C7.15055 16.0696 7.15055 15.4296 7.54055 15.0396L10.5905 11.9996L7.54055 8.95957C7.15055 8.56957 7.15055 7.92957 7.54055 7.53957C7.93055 7.14957 8.57055 7.14957 8.96055 7.53957L12.0005 10.5896L15.0405 7.53957C15.4305 7.14957 16.0705 7.14957 16.4605 7.53957C16.8505 7.92957 16.8505 8.56957 16.4605 8.95957L13.4105 11.9996L16.4605 15.0396C16.8505 15.4296 16.8505 16.0696 16.4605 16.4596C16.0705 16.8496 15.4305 16.8496 15.0405 16.4596L12.0005 13.4196L8.96055 16.4596Z' fill='white'/%3E%3C/svg%3E%0A") !important;
    }

    li:hover{
      text-decoration: underline;
    }

  }

  &-btn{
    margin-top: 30px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    .btn{
      padding: 14px;
      font-size: 13px;
      line-height: 17px;
      
      @media screen and (min-width: 480px) {
        padding: 15px;
      }

      @media screen and (min-width: 640px) {
        font-size: 16px;
        line-height: 21px;
        padding: 20px 17px;
      }

      @media screen and (min-width: 960px) {
        padding: 20px 27px;
      }

      @media screen and (min-width: 1152px) {
        font-size: 20px;
        line-height: 27px;
        padding: 23px 27px;
      }
      
    }

  }

  @media screen and (min-width: 480px) {
    &-image {
      margin-left: auto;
      max-width: 460px;
    }

    &-btn{
      max-width: 540px;
    }

    &-text {
      max-width: 540px;

      &--fullwidth{
        width: 100%;
        max-width: 100%;
        margin: 0;
      }

    }
  }

  @media screen and (min-width: 640px) {
    &-text ul.list{
      font-size: 16px !important;
      li{
        padding: 6px 0 6px 40px !important;
      }
      li::before{
        width: 20px !important;
        height: 20px !important;
      }

    }
  }

  @media screen and (min-width: 830px) {
    .container {
      display: flex;
      flex-wrap: wrap;
      align-items: flex-start;
    }

    .section-h2 {
      width: 100%;
      order: 1;
    }

    &-image {
      width: 540px / 910px * 100%;
      order: 3;
      max-width: 660px;

      &--small{
        width: 460px / 1160px * 100%;
        max-width: 460px;
      }

    }

    &-btn{
      width: 420px / 910px * 100%;
      max-width: 530px;
      order: 3;
    }

    &-text {
      margin-top: 30px;
      width: 420px / 910px * 100%;
      order: 2;
      margin-right: -100px;
      max-width: 530px;

      &--large{
        width: 730px / 1160px * 100%;
        max-width: 730px;
        margin-top: 20px;
      }

      &--fullwidth{
        width: 100%;
        max-width: 100%;
        margin: 0;

        .list{
          columns: 2;
          column-gap: 40px;
          li{
            break-inside: avoid;
          }
        }

      }

    }

    &.with-btn{
      .detailAgainst-text{
        margin-bottom: auto;
      }
      .detailAgainst-image{
        margin-bottom: -61px;
      }
      .detailAgainst-btn{
        margin-top: 0;
      }
    }

  }

  @media screen and (min-width: 1152px) {

    &.with-btn{
      .detailAgainst-text{
        margin-bottom: 58px;
      }
      .detailAgainst-image{
        margin-bottom: -73px;
      }
      .detailAgainst-btn{
        margin-top: 0;
      }
    }

    &-text ul.list{
      font-size: 20px !important;

      li{
        padding-left: 40px !important;
      }

      li::before{
        width: 24px !important;
        height: 24px !important;
      }

    }
  }


}