.fleets-banner {
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
  }

  &__figure{
    position: absolute;
    top: 0;
    left: -25px;
    z-index: -1;
    width: 206px;
    height: 212px;
    transform: translateY(-100%);

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
    }

    @media screen and (min-width: 640px) {
    }

    @media screen and (min-width: 1152px) {
      width: 403px;
      height: 350px;
      left: 0;
      bottom: 0;
      top: auto;
      transform: translate(-50%,20%);
    }

  }

  &__content {
    background: #CEDFF9;
    border-radius: 10px;
    padding: 20px 15px 0;
    width: 100%;
    position: relative;
    min-height: 520px;
  }

  .section-h3 {
    margin-bottom: 35px;
    text-align: center;
    position: relative;
    z-index: 1;
    font-size: 18px;
    line-height: 130%;
    letter-spacing: 0.05em;
    width: 100%;

    span.blue {
      font-size: 23px;
      line-height: 130%;
    }

  }

  &__price {
    display: flex;
    align-items: flex-start;
    position: relative;
    z-index: 1;

    .price {
      position: relative;
      z-index: 1;
      display: flex;
      flex-direction: column;
    }

    .price-old {
      font-weight: 700;
      font-size: 25px;
      line-height: 150%;
      text-align: center;
      letter-spacing: 0.05em;
      margin-bottom: 15px;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        margin: 0 auto;
        display: block;
        width: 70%;
        height: 3px;
        background: #FF7020;
        border-radius: 2px;
        transform: rotate(-10deg) translateY(-5px);

        @media screen and (min-width: 1152px) {
          height: 5px;
        }

      }
    }

    .price-new {
      font-weight: 700;
      font-size: 30px;
      line-height: 150%;
      letter-spacing: 0.05em;
      color: #FFFFFF;
      text-align: center;
      background: #8C52FF;
      border-radius: 10px;
      padding: 0 13px;
    }

    .sale {
      background: #FF7020;
      border-radius: 120px;
      font-weight: 700;
      font-size: 35px;
      line-height: 150%;
      text-align: center;
      color: #FFFFFF;
      width: 97px;
      height: 97px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: -15px;
      margin-top: -20px;
    }
  }

  &__image {
    width: 232px;
    height: 260px;
    margin-left: auto;
    position: absolute;
    bottom: 54px;
    right: 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: bottom right;
    }
  }

  &__timer {
    background: #FFFFFF;
    box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    padding: 15px;
    max-width: 280px;
    width: 100%;
    z-index: 1;
    position: absolute;
    bottom: -30px;
    left: 0;
    margin: 0 10px;

    & > * {
      text-align: center;
      position: relative;
    }

    .divider {
      position: relative;
      flex-shrink: 0;

      &::after {
        content: ":";
        display: block;
        font-weight: 700;
        font-size: 20px;
        line-height: 150%;
        letter-spacing: 0.05em;
      }
    }

    .value {
      font-weight: 700;
      font-size: 25px;
      line-height: 150%;
      letter-spacing: 0.05em;
      color: #FFFFFF;
      margin-bottom: 9px;

      span {
        position: relative;
        margin: 0 2px;
        z-index: 1;
        padding: 3px;

        &::before {
          content: "";
          display: block;
          top: 0;
          left: 0;
          right: 0;
          height: 50%;
          width: 100%;
          position: absolute;
          background: linear-gradient(180deg, #4E4E4E 0%, #151515 100%);
          box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.25);
          border-radius: 4px 4px 1px 1px;
          z-index: -1;
        }

        &::after {
          content: "";
          display: block;
          bottom: 0;
          left: 0;
          right: 0;
          height: 50%;
          width: 100%;
          position: absolute;
          background: linear-gradient(180deg, #393939 0%, #151515 100%);
          box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.25);
          border-radius: 1px 1px 4px 4px;
          z-index: -1;
        }
      }
    }

    .caption {
      font-size: 10px;
      line-height: 150%;
      letter-spacing: 0.05em;
      color: #262626;
    }

  }

  .btn {
    margin-top: 60px;
    min-width: 220px;
    padding: 16px 20px;
    background: #8C52FF !important;


    @media screen and (min-width: 640px) {
      min-width: 260px;
      padding: 20px 27px;
      font-size: 16px;
      line-height: 21px;
    }

    @media screen and (min-width: 1152px) {
      font-size: 20px;
      line-height: 27px;
      padding: 22px 30px;
      min-width: 340px;
      margin-top: 40px;
    }

  }

  @media screen and (min-width: 330px) {
    &__image {
      bottom: 0;
      width: 254px;
      height: 325px;
    }
  }

  @media screen and (min-width: 480px) {
    &__content {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      min-height: 420px;
    }

    &__price {
      flex-direction: column;
      align-items: center;
      justify-content: center;

      .sale {
        order: 1;
        margin: 0 0 7px;
      }

      .price {
        order: 2;
      }
    }
  }

  @media screen and (min-width: 640px) {

    &-content {
      min-height: 460px;
      padding: 20px 15px !important;
    }

    .section-h3 {
      text-align: right;
      max-width: 445px;
      margin-left: auto;
      margin-bottom: 0;
      font-size: 30px;

      span.blue {
        font-size: 38px;
      }
    }

    &__price {
      padding-bottom: 20px;

      .sale {
        width: 151px;
        height: 151px;
        font-size: 60px;
      }

      .price-old {
        font-size: 35px;
      }

      .price-new {
        font-size: 45px;
      }
    }

    &__timer {
      max-width: 420px;
      width: 100%;
      position: relative;
      bottom: auto;
      margin: 0 0 20px 0;


      .value {
        font-size: 35px;
      }

      .divider::after {
        font-size: 35px;
      }

      .caption {
        font-size: 14px;
      }

    }

    &__image {
      width: 344px;
      height: 409px;
    }

  }

  @media screen and (min-width: 960px) {
    &__content {
      padding: 30px 420px 20px 20px !important;
      min-height: 460px;
    }

    .section-h3 {
      text-align: left;
      margin-bottom: 24px;
      position: relative;
      z-index: 2;
      max-width: 100%;
    }

    &__price {
      flex-direction: row;
      align-items: flex-start;
      width: 100%;
      padding-bottom: 32px;

      .sale {
        order: 2;
        margin-right: -50px;
        margin-top: -50px;
      }

      .price {
        order: 1;
      }

    }

    &__image {
      width: 420px;
      height: 499px;
    }
    &__timer {
      margin-bottom: 0;
    }
  }

  @media screen and (min-width: 1152px) {
    &__content {
      padding: 35px 360px 35px 30px;
      min-height: 0;
    }

    .section-h3 {
      text-align: left;
      max-width: 460px;
      margin-left: 0;
      margin-bottom: 40px;
    }

    &__price {
      flex-direction: column;
      align-items: center;
      position: absolute;
      top: -45px;
      bottom: 35px;
      left: 460px + 63px;
      width: auto;
      max-width: 260px;
      padding-bottom: 0;

      .sale {
        order: 1;
        margin: 0;
        margin-bottom: 12px;
        font-size: 70px;
        width: 175px;
        height: 175px;
      }

      .price {
        order: 2;

        &-old {
          margin-bottom: 10px;
        }

        &-new {
          font-size: 50px;
        }
      }

    }

    &__timer {
      margin-bottom: 0;
    }

    &__image {
      width: 322px;
      height: 360px;
      margin-right: 38px;
    }
  }


}

