.detailPrices{
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .period-block{
    @media screen and (min-width: 640px) {

      .price-block__item .name,
      .price-block__item .price{
        font-size: 16px;
      }

      &__caption{
        font-size: 16px;
        br{
          display: none;
        }
      }
      &__text{
        font-size: 16px;
      }
    }

    @media screen and (min-width: 830px) {
      padding: 0 28px 30px 23px;
    }

    @media screen and (min-width: 1152px) {
      align-items: flex-start;

      .price-block__item .name,
      .price-block__item .price{
        font-size: 20px;
      }

      .price-block__item .price{
        display: flex;
        align-items: center;

        span:not(.blue){
          margin-right: 24px;
        }

      }

      &__caption{
        font-size: 20px;
        padding: 13px 28px;
        br{
          display: none;
        }
      }
      &__text{
        font-size: 20px;
      }
    }

  }

}