.detailProps {
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .section-h2 {
    margin-bottom: 40px;
  }

  .container{
    position: relative;
  }

  &-figure{
    position: absolute;
    top: 100%;
    right: -30px;
    z-index: -1;
    width:218px;
    height: 205px;
    transform: translateY(-65%);

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    &.top{
      top: 10%;
    }

    @media screen and (min-width: 480px) {
      width: 274px;
      height: 258px;
      right: -35px;
      transform: translateY(-60%);
    }

    @media screen and (min-width: 640px) {
      width: 327px;
      height: 308px;
      transform: translateY(-50%);
    }

    @media screen and (min-width: 1152px) {
      width: 408px;
      height: 366px;
      top: 100%;
      transform: none;
      right: auto;
      left: -130px;

      &.top{
        top: 0;
      }

    }

  }

  .props-items {
    counter-reset: section;

    &__item {
      background: #FFFFFF;
      box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      padding: 15px 10px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 50px;
      position: relative;

      &:last-child{
        margin-bottom: 0;
      }

      .icon {
        width: 52px;
        height: 52px;
        margin-top: -20px;
        flex-shrink: 0;
        position: absolute;
        top: 0;
        left: 10px;

        svg {
          width: 100%;
          height: auto;
        }
      }

      .name {
        padding-left: 67px;
        font-weight: 700;
        font-size: 13px;
        line-height: 150%;
        letter-spacing: 0.05em;
        padding-right: 10px;
        color: #5271FF;

        &-black{
          color: #262626;
        }

      }

      .text {
        margin-top: 10px;
        font-size: 13px;
        line-height: 150%;
        letter-spacing: 0.05em;
      }

    }
  }

  @media screen and (min-width: 640px) {

    .props-items {
      &__item {
        padding: 20px 30px;
        margin-bottom: 55px;

        .icon {
          width: 80px;
          height: 80px;
          margin-top: -30px;
          left: 20px;
        }

        .name {
          font-size: 16px;
          padding-left: 87px;
        }

        .text {
          font-size: 16px;
        }

      }
    }

  }

  @media screen and (min-width: 830px) {

    .props-items {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -10px;
      width: calc(100% + 20px);
      justify-content: center;

      &__item {
        width: calc(50% - 20px);
        margin: 30px 10px;
        max-width: 640px;
        flex-grow: 1;
      }
    }

  }

  @media screen and (min-width: 1152px) {

    .props-items {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -20px;
      width: calc(100% + 40px);
      justify-content: center;

      &__item {
        width: calc(50% - 40px);
        margin: 35px 20px;
        max-width: 700px;
        flex-grow: 1;
        padding: 30px 26px 30px;

        .name {
          font-size: 20px;
        }

        .text {
          font-size: 20px;
        }

      }
    }

  }

}