.detailService{
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .container{
    position: relative;
  }

  &-figure{
    position: absolute;
    top: -30px;
    left: -30px;
    z-index: -1;
    width:206px;
    height: 212px;
    transform: translateY(-40%);

    &.bottom{
      top: auto;
      left: auto;
      bottom: -30px;
      right: -30px;
    }

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
      width: 259px;
      height: 280px;
    }

    @media screen and (min-width: 640px) {
      width: 310px;
      height: 346px;
      top: -15px;
    }

    @media screen and (min-width: 1152px) {
      width: 452px;
      height: 371px;
      top: 0;
      transform: none;
      left: -160px;

      &.bottom{
        right: -180px;
        bottom: -100px;
      }

    }

  }

  .section-h2{
    margin-bottom: 40px;
  }

  &-item{
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    &__image{
      max-width: 400px;
      margin: 0 auto;
      padding: 0 10px;
      margin-bottom: -20px;
      width: 100%;

      img{
        width: 100%;
        height: auto;
        border-radius: 10px;
      }
    }

    &__caption{
      font-weight: 700;
      font-size: 13px;
      line-height: 150%;
      letter-spacing: 0.05em;
      color: #FFFFFF;
      padding: 13px 10px 12px;
      position: relative;
      z-index: 1;
      background: #8C52FF;
      border-radius: 10px;
    }

    &__text{
      margin: -10px 10px 0;
      background: #FFFFFF;
      box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      padding: 30px 10px 20px;
      font-size: 13px;
      line-height: 150%;

      ul{
        padding: 9px 0;

        @media screen and (min-width: 480px) {
          columns: 2;
          column-gap: 46px;
        }

        @media screen and (min-width: 640px) {
          columns: 3;
          column-gap: 20px;
        }

        li{
          position: relative;
          padding: 6px 0 6px 25px;
        }

        li::before{
          content: "";
          display: block;
          position: absolute;
          top: 50%;
          left: 0;
          width: 14px;
          height: 14px;
          transform: translateY(-50%);
          background-repeat: no-repeat;
          background-position: center;
          background-image: url("data:image/svg+xml,%3Csvg width='14' height='11' viewBox='0 0 14 11' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.370421 6.23517C-0.123474 5.74016 -0.123474 4.94054 0.370421 4.44553C0.851651 3.95053 1.66214 3.95053 2.15604 4.43284L4.96743 7.26326L11.844 0.371254C12.3379 -0.123751 13.1357 -0.123751 13.6296 0.371254C14.1235 0.86626 14.1235 1.67858 13.6296 2.17358L5.86658 9.95405C5.37268 10.4364 4.57485 10.4364 4.08096 9.95405L0.370421 6.23517Z' fill='%238C52FF'/%3E%3C/svg%3E%0A");
        }

      }

    }

    @media screen and (min-width: 480px) {

      &__image{
        position: relative;
        z-index: 1;
      }

      &__caption{
        margin-bottom: -165px;
      }

      &__text{
        padding-top: 205px;
      }

    }

    @media screen and (min-width: 640px) {
      background: #FFFFFF;
      box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: flex-start;

      &__image{
        width: 300px;
        height: 300px;
        margin: -20px 0 0px -10px;
        padding: 0;

        img{
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      &__text{
        background: none;
        box-shadow: none;
        padding-top: 20px;
        font-size: 16px;
      }

      &__caption{
        margin-left: -20px;
        margin: 0px -10px 0 -20px;
        font-size: 16px;
        width: calc(100% - 260px);
        position: relative;
        z-index: 2;
      }
    }

    @media screen and (min-width: 830px) {
      align-items: flex-end;
      background: none;
      box-shadow: none;

      &__caption{
        margin-bottom: 30px;
        padding: 21px 15px;
      }

      &__text{
        background: #FFFFFF;
        box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
        border-radius: 10px;
        padding: 65px 20px 25px;
        margin-top: -45px;
      }

    }

    @media screen and (min-width: 1152px) {

      &__caption{
        font-size: 20px;
        padding: 20px;
      }

      &__text{
        font-size: 20px;
      }

    }


  }

  @media screen and (min-width: 640px){
    &.image-from-right{
      .detailService-item__caption{
        order: 1;
        margin: 0 -20px 0 -10px;
      }
      .detailService-item__image{
        order: 2;
        margin: -20px -10px 0 0;
      }
      .detailService-item__text{
        order: 3;
      }
    }
  }

  @media screen and (min-width: 830px){
    &.image-from-right{
      .detailService-item__caption{
        margin-bottom: 30px;
      }
    }
  }

  @media screen and (min-width: 1152px) {

    &.image-from-right{
      .section-h2{
        margin-right: 300px;
        margin-left: 0;
      }
    }

    .section-h2{
      margin-left: 300px;
      margin-bottom: 0px;
    }
  }


}