.detailTypes {
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .container{
    position: relative;
  }

  &-figure{
    position: absolute;
    top: 100%;
    right: -30px;
    z-index: -1;
    width:217px;
    height: 202px;
    transform: translateY(-25%);

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
      width: 273px;
      height: 254px;
      right: -35px;
    }

    @media screen and (min-width: 640px) {
      width: 326px;
      height: 304px;
      transform: translateY(-30%);
    }

    @media screen and (min-width: 1152px) {

      &.bottom{
        top: 80%;
      }

      width: 437px;
      height: 456px;
      top: 100%;
      right: -160px;
      transform: none;
    }

  }

  .tabs__toggles {
    justify-content: center;
    margin: 0 -5px;
  }

  .tabs__toggle {
    width: calc(50% - 10px);
    margin: 5px;

    @media screen and (min-width: 480px) {
      width: calc(33.33% - 10px);
    }

    @media screen and (min-width: 640px) {
      font-size: 16px;
    }

    @media screen and (min-width: 1152px) {
      width: calc(20% - 10px);
      font-size: 20px;
    }

  }

  .tabs__contents {
    margin-top: 30px;
  }

  &-text {
    background: #FFFFFF;
    box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    font-size: 13px;
    line-height: 150%;
    letter-spacing: 0.05em;
    color: #262626;
    padding: 20px 10px;
  }

  &-image{
    display: none;
  }

  .tabs__block .tabs__content.is-active{
    display: flex !important;
  }

  @media screen and (min-width: 640px) {
    &-text{
      font-size: 16px;
      width: calc(100% - 190px);
      margin-top: 30px;
      z-index: 1;
      position: relative;
      padding: 30px 20px 13px;
    }
    &-image{
      display: block;
      width: 200px;
      height: 200px;
      position: relative;
      margin-left: -10px;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  @media screen and (min-width: 830px) {
    &-text{
      width: calc(100% - 250px);
    }
    &-image{
      width: 260px;
      height: 260px;
    }
  }

  @media screen and (min-width: 1152px) {
    &-text{
      width: calc(100% - 340px);
      font-size: 20px;
      padding: 23px 40px 26px 20px;
    }
    &-image{
      width: 360px;
      height: 360px;
      margin-left: -20px;
    }

  }

}