.homeResult{
  padding: 0 0 70px;
  position: relative;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 40px 0 120px;
  }

  .container{
    position: relative;
  }

  &-figure{
    position: absolute;
    top: 100%;
    right: -10px;
    width: 223px;
    height: 228px;
    transform: translateY(-45%);
    z-index: -1;

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
      bottom: auto;
      right: -25px;
      width: 296px;
      height: 302px;
    }

    @media screen and (min-width: 640px) {
      width: 305px;
      height: 319px;
      transform: translateY(-60%);
    }

    @media screen and (min-width: 1152px) {
      width: 509px;
      height: 527px;
      right: -160px;
      top: 50%;
      transform: translateY(-50%);
    }

  }

  &-banner{
    position: relative;
    padding: 30px 10px 20px;
    background: #CA6BE5;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    text-align: right;
    z-index: 1;

    & > * {
      position: relative;
      z-index: 1;
    }

    &__image{
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      max-width: 220px;
      img{
        width: 100%;
        object-fit: cover;
        object-position: right;
      }
    }

    &__title{
      color: #fff;
      margin-bottom: 5px;
      max-width: 263px;
    }

    &__desc{
      margin-bottom: 21px;
      font-size: 13px;
      line-height: 130%;
      letter-spacing: 0.05em;
      color: #FFFFFF;
      max-width: 225px;
    }

    &__phones{
      margin-bottom: 60px;
      max-width: 52%;
      .phone{
        margin-bottom: 26px;
        &-title{
          font-size: 13px;
          line-height: 150%;
          letter-spacing: 0.05em;
          color: #FFFFFF;
        }
        &-value{
          font-weight: 700;
          font-size: 16px;
          line-height: 150%;
          letter-spacing: 0.05em;
          color: #FFFFFF;
          & > *{
            color: #FFFFFF;
          }
        }
      }
    }

    &__btn{
      .btn{
        padding: 15px 26px;
        position: relative;
        border-radius: 15px;
        &::after{
          content: "";
          position: absolute;
          display: block;
          width: 100%;
          height: 100%;
          background: #E1CAE8;
          box-shadow: 0px 3px 25px rgba(0, 0, 0, 0.35);
          border-radius: 15px;
          z-index: -1;
          transform: translateY(3px);
        }
      }
    }

    @media screen and (min-width: 480px) {
      padding: 42px 23px;
      &__image{
        max-width: 250px;
        top: 0;
        img{
          width: 100%;
          height: 100%;
        }
      }
      &__phones{
        margin-bottom: 0;
        .phone{
          &-value{
            font-size: 20px;
          }
        }
      }
    }

    @media screen and (min-width: 640px){

      &__title{
        max-width: 394px;
      }

      &__desc{
        font-size: 16px;
      }

      &__image{
        max-width: 52%;
      }

      &__phones{
        margin-bottom: 0;
        .phone{
          &-title{
            font-size: 16px;
            text-align: center;
          }
          &-value{
            font-size: 30px;
          }
        }
      }

      &__btn{
        .btn{
          padding: 17px 27px;
          font-size: 16px;
          line-height: 21px;
        }
      }

    }

    @media screen and (min-width: 768px) {
      &__image{
        max-width: 60%;
        img{
          object-fit: contain;
          object-position: left bottom;
        }
      }
    }

    @media screen and (min-width: 960px){
      text-align: center;
  
      &__image{
        top: -21px;
        img{
          object-fit: contain;
          object-position: left;
        }
      }


      &__title,
      &__desc,
      &__phones,
      &__btn{
        max-width: 394px;
        width: 100%;
        text-align: center;
      }

      &__btn{
        display: flex;
        justify-content: center;
      }

    }

    @media screen and (min-width: 1152px){
      text-align: left;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: center;
      padding-left: 340px;
      border-radius: 20px;

      &__image{
        top: -87px;
        max-width: 320px !important;

        img{
          object-position: bottom;
        }
      }

      & > *{
        max-width: 100%;
        text-align: left;
      }

      &__phones{
        margin-bottom: 0;
        display: flex;
        justify-content: space-between;
        max-width: calc(100% - 320px);
        margin-right: auto;
        .phone{
          margin-bottom: 0;
          width: calc(50% - 15px);
          &-title{
            font-size: 16px;
            text-align: left;
          }
          &-value{
            font-size: 20px;
          }
        }
      }

      &__btn{
        max-width: 300px;

        .btn{
          font-size: 20px;
          line-height: 27px;
          padding: 14px 18px;
        }

      }

    }

    @media screen and (min-width: 1280px){

      &__phones{
        .phone{
          &-value{
            font-size: 25px;
          }
        }
      }

    }

  }
}