.detailAmount{
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .container{
    position: relative;
  }

  &-figure{
    position: absolute;
    display: none;
    z-index: -1;

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 1152px) {
      display: block;
      width: 489px;
      height: 366px;
      top: -20px;
      left: -130px;
      transform: none;
    }

  }

  .how-much{
    table{
      width: 100%;
    }
    &__image{
      display: none;
    }

    .btn{
      @media screen and (min-width: 480px) {
        padding: 15px;
      }

      @media screen and (min-width: 640px) {
        font-size: 16px;
        line-height: 21px;
        padding: 20px 17px;
      }

      @media screen and (min-width: 960px) {
        padding: 20px 27px;
      }

      @media screen and (min-width: 1152px) {
        font-size: 20px;
        line-height: 27px;
        padding: 23px 27px;
      }
    }


  }

  @media screen and (min-width: 640px) {
    .how-much table tr td{
      font-size: 16px !important;
      line-height: 21px !important;
    }
  }

  @media screen and (min-width: 960px) {
    .how-much{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      &__table{
        width: 610px / 910px * 100%;
        max-width: 860px;
        margin-right: -7%;
      }
      &__image{
        display: block;
        width: 359px / 910px * 100%;
        max-width: 518px;

        img{
          width: 100%;
          height: 100%;
          object-fit: contain;
        }

        .caption{
          margin-top: 30px;
          text-align: right;
          font-size: 16px;
          line-height: 150%;
          letter-spacing: 0.05em;
          max-width: 286px;
          margin-left: auto;

          span.blue{
            margin-top: 5px;
            display: block;
            font-size: 20px;
          }
        }

      }
    }
  }
  @media screen and (min-width: 1152px) {
    .how-much{
      table tr td{
        font-size: 20px !important;
        line-height: 150% !important;
        padding: 8px;
      }
      &__table{
        width: 860px / 1200px * 100%;
        max-width: 860px;
        margin-right: -140px;
        padding: 30px 105px 19px 30px;
      }
      &__image{
        display: block;
        width: 518px / 1200px * 100%;
        max-width: 518px;
        margin-right: -40px;
      }
      .btn{
        display: none;
      }
    }
  }
}