.detailAdvantages{
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .container{
    position: relative;
  }

  &-figure{
    position: absolute;
    top: 0;
    right: -30px;
    z-index: -1;
    width:193px;
    height: 194px;
    transform: translateY(-40%);

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
      width: 243px;
      height: 244px;
      right: -25px;
      transform: translateY(-75%);
    }

    @media screen and (min-width: 640px) {
      width: 290px;
      height: 271px;
      transform: translateY(-25%);
    }

    @media screen and (min-width: 1152px) {
      width: 374px;
      height: 366px;
      top: 0;
      right: -160px;
    }

  }

  .recommend-block{
    @media screen and (min-width: 640px) {
      padding: 20px 20px 28px;
      &__caption {
        font-size: 16px;
        margin-top: 0;
        margin-right: -30px;
      }

      &__title {
        font-size: 23px;
      }

      ul.list li,
      ul.list li a{
        font-size: 16px;
        line-height: 150%;
      }

    }
    @media screen and (min-width: 1152px) {
      padding: 20px 30px 30px;
      &__caption {
        font-size: 20px;
        margin-right: -44px;
        padding: 13px 21px;
      }

      &__title {
        font-size: 25px;
      }

      ul.list li,
      ul.list li a{
        font-size: 20px;
      }

      ul.list li{
        padding-left: 40px;
      }

      ul.list li::before{
        width: 24px;
        height: 24px;

      }

    }
  }

}