@font-face {
  font-family: 'Montserrat';
  src: url('/fonts/subset-Montserrat-ExtraBold.woff2') format('woff2');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

.detailCaption{
  &-caption{
    margin: 0 auto;
  }
}

.detailIntro {
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

}

.detailWorks {
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .container {
    position: relative;
  }

  &-figure {
    position: absolute;
    top: calc(100% - 60px);
    left: -20px;
    z-index: -1;
    width: 150px;
    height: 230px;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
      top: calc(100% - 100px);
      left: -25px;
      width: 212px;
      height: 288px;
    }

    @media screen and (min-width: 640px) {
      width: 325px;
      height: 345px;
      top: 0;
      left: -35px;
      transform: translateY(50%);
    }

    @media screen and (min-width: 1152px) {
      width: 438px;
      height: 416px;
      left: -100px;
      transform: translateY(-25%);
    }

  }


}

.detailBlock {
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .section-h2 {
    margin-bottom: 30px;
    font-family: 'Montserrat', sans-serif;


    @media screen and (min-width: 830px) {
      margin-bottom: 40px;

      &+.compare-caption {
        margin-top: -20px;
      }

    }

    @media screen and (min-width: 1152px) {
      margin-bottom: 60px;

      &+.compare-caption {
        margin-top: -40px;
      }

    }






  }

  .prepare-block {
    margin: 0 10px;

    &__caption {
      background: #8C52FF;
      border-radius: 10px;
      font-size: 13px;
      line-height: 150%;
      font-weight: 700;
      letter-spacing: 0.05em;
      color: #FFFFFF;
      padding: 13px;
      margin: 0 auto;
      margin-left: -10px;
      margin-right: 20px;
      margin-bottom: -40px;
      position: relative;
      z-index: 1;
      max-width: 720px;
    }

    &__text {
      padding: 60px 10px 20px;
      background: #FFFFFF;
      box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
      border-radius: 10px;

      ul li {
        padding: 6px 0 6px 25px;
        position: relative;
        font-size: 12px;
        line-height: 150%;
        letter-spacing: 0.05em;

        &::before {
          content: "";
          position: absolute;
          top: 8px;
          left: 0;
          width: 15px;
          height: 15px;
          background-size: 100%;
          background-repeat: no-repeat;
          background-image: url("data:image/svg+xml,%3Csvg width='15' height='15' viewBox='0 0 15 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.9375 5.225L5.225 0.9375C6.475 -0.3125 8.525 -0.3125 9.775 0.9375L14.0625 5.225C15.3125 6.475 15.3125 8.525 14.0625 9.775L9.775 14.0625C8.525 15.3125 6.475 15.3125 5.225 14.0625L0.9375 9.775C-0.3125 8.525 -0.3125 6.475 0.9375 5.225Z' fill='%238C52FF'/%3E%3Cpath d='M4.4377 9.26868C4.19395 8.02493 4.19395 7.63118 4.4377 7.38743C4.6752 7.14368 5.0752 7.14368 5.31895 7.38118L6.70644 8.77493L10.1002 5.38118C10.3439 5.13743 10.7377 5.13743 10.9814 5.38118C11.2252 5.62493 11.2252 6.02493 10.9814 6.26868L7.15019 10.0999C6.90645 10.3374 6.51269 10.3374 6.26894 10.0999L4.4377 8.26868Z' fill='white'/%3E%3C/svg%3E%0A") !important;
        }
      }

    }

    @media screen and (min-width: 640px) {

      &__caption,
      &__text ul li {
        font-size: 16px;
      }

      &__text {
        padding: 60px 20px 20px;
      }

      &__text ul li {
        padding: 6px 0 6px 40px;

        &::before {
          width: 20px;
          height: 20px;
        }
      }

    }

    @media screen and (min-width: 1152px) {

      &__caption,
      &__text ul li {
        font-size: 20px;
      }

      &__caption {
        margin: 0 auto -20px;
      }

      &__text {
        padding: 50px 20px 30px;
      }

      &__text ul li {
        &::before {
          width: 24px;
          height: 24px;
        }
      }

    }

  }

  .intro {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    &-caption {
      background: #8C52FF;
      border-radius: 10px;
      padding: 10px 15px;
      font-weight: 700;
      font-size: 13px;
      line-height: 150%;
      letter-spacing: 0.05em;
      color: #FFFFFF;
      margin: 0 10px -20px;
      position: relative;
      z-index: 1;

      &--pink {
        background: #CB6CE6;
      }
    }

    &-text {
      padding: 40px 10px 15px;
      font-size: 13px;
      line-height: 150%;
      letter-spacing: 0.05em;
      background: #FFFFFF;
      box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
    }

    @media screen and (min-width: 640px) {
      &-caption {
        padding: 20px;
        font-size: 16px;
        max-width: 434px / 600px * 100%;
      }

      &-text {
        font-size: 16px;
      }
    }

    @media screen and (min-width: 830px) {
      &-text {
        padding: 40px 20px 20px;
      }
    }

    @media screen and (min-width: 1152px) {
      display: flex;
      flex-direction: row;

      &.same-width {
        .intro-caption {
          margin: 20px -20px 0 0;
          max-width: 580px;
        }

        .intro-text {
          padding: 20px 20px 20px 40px;
          max-width: 600px;
        }
      }

      &-caption {
        margin: 20px -20px 0 0;
        max-width: 440px;
        font-size: 20px;
      }

      &-text {
        padding: 20px 20px 20px 40px;
        max-width: 740px;
        font-size: 20px;
      }
    }

  }

  .video-block {
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    max-width: 620px;
    margin: 0 auto;

    &__preview {
      img {
        width: 100%;
        height: auto;
      }
    }

    &__play {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      background: rgba(39, 20, 77, 0.4);

      svg {
        width: 75px;
        height: 75px;
        object-fit: contain;
        transition: all 0.25s ease;

        &:hover {
          transform: scale(1.1);
        }

        @media screen and (min-width: 480px) {
          height: 110px;
          width: 110px;
        }

        @media screen and (min-width: 640px) {
          height: 115px;
          width: 115px;
        }

        @media screen and (min-width: 960px) {
          height: 155px;
          width: 155px;
        }

        @media screen and (min-width: 1152px) {
          height: 240px;
          width: 240px;
        }

      }

    }

    @media screen and (min-width: 830px) {
      max-width: 960px;
    }

  }

  .how-works {
    margin: 0 10px;

    @media screen and (min-width: 480px) {
      margin: 0;
    }

    @media screen and (min-width: 830px) {
      padding: 75px 0 0;
    }

    @media screen and (min-width: 1152px) {
      padding: 45px 0 0;
    }

    &__item {
      background: #FFFFFF;
      box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      display: flex;
      flex-wrap: wrap;
      margin: 25px 0;

      &:last-child {
        margin-bottom: 0;
      }

      &.even {
        .highlight {
          margin-right: auto;
          margin-left: -10px;
        }
      }

      .text {
        padding: 20px 10px;
        font-size: 13px;
        line-height: 150%;
        letter-spacing: 0.05em;

        @media screen and (min-width: 640px) {
          font-size: 16px;

          ul li::before {
            top: 12px;
          }

        }

        @media screen and (min-width: 1152px) {
          font-size: 20px;

          ul li::before {
            top: 15px;
          }

        }

        ul {
          li {
            padding: 6px 0 6px 20px;
            position: relative;

            &::before {
              content: "";
              position: absolute;
              top: 9px;
              left: 0;
              width: 6px;
              height: 6px;
              background: #8C52FF;
              border-radius: 4px;
            }
          }
        }

      }

      .highlight {
        background: #8C52FF;
        border-radius: 10px;
        padding: 12px 10px;
        font-size: 13px;
        line-height: 150%;
        letter-spacing: 0.05em;
        color: #FFFFFF;
        max-width: calc(100% - 5px);
        margin-left: auto;
        margin-right: -10px;
        margin-bottom: -10px;
        align-self: center;
        font-weight: bold;
      }

      @media screen and (min-width: 480px) {
        .highlight {
          margin-bottom: 0;
          align-self: flex-start;
        }
      }

      @media screen and (min-width: 640px) {
        position: relative;
        padding-top: 20px;

        &.even {
          .highlight {
            margin-left: -10px;
            margin-right: auto;
            max-width: calc(100% - 260px);
          }
        }

        .highlight {
          font-size: 16px;
          margin-right: -10px;
          margin-left: auto;
          max-width: calc(100% - 260px);
        }

        .text {
          font-size: 16px;
          padding: 20px;
        }
      }

      @media screen and (min-width: 1152px) {

        .highlight {
          font-size: 20px;
          padding: 13px 20px;
        }

        .text {
          font-size: 20px;
        }

      }

    }


  }

  .period-block {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background: #FFFFFF;
    box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    padding: 0 10px 20px;
    margin: 10px 0 40px;

    &__caption {
      font-weight: 700;
      font-size: 13px;
      line-height: 150%;
      letter-spacing: 0.05em;
      color: #FFFFFF;
      margin-top: -10px;
      padding: 8px 14px;
      background: #8C52FF;
      border-radius: 10px;
    }

    &__text {
      margin-top: 20px;
      font-size: 13px;
      line-height: 150%;
      letter-spacing: 0.05em;
      width: 100%;

      @media screen and (min-width: 640px) {
        font-size: 16px;

        ul li::before {
          top: 12px;
        }

      }

      @media screen and (min-width: 1152px) {
        font-size: 20px;

        ul li::before {
          top: 15px;
        }

      }

      ul {
        li {
          padding: 6px 0 6px 20px;
          position: relative;

          &::before {
            content: "";
            position: absolute;
            top: 9px;
            left: 0;
            width: 6px;
            height: 6px;
            background: #8C52FF;
            border-radius: 4px;
          }
        }
      }

    }

    &__group {
      .period-block:last-child {
        margin-bottom: 0;
      }
    }


  }

  .price-block {
    align-items: flex-end;
    margin-bottom: 0;

    &__title {
      font-weight: 700;
      font-size: 16px;
      line-height: 150%;
      letter-spacing: 0.05em;
      color: #8C52FF;
      margin-bottom: 5px;

      @media screen and (min-width: 640px) {
        font-size: 20px;
      }

      @media screen and (min-width: 1152px) {
        font-size: 25px;
      }

    }

    &__items {
      margin-top: 30px;
      display: flex;
      flex-direction: column;
      width: 100%;
    }

    &__item {
      display: flex;
      align-items: center;
      padding: 10px 0;
      border-bottom: 1px solid #CCCCCC;

      .name {
        margin-right: auto;
        font-size: 13px;
        line-height: 150%;
        letter-spacing: 0.05em;
        padding-right: 15px;
      }

      .price {
        text-align: right;
        flex-shrink: 0;
        font-size: 13px;
        line-height: 150%;
        letter-spacing: 0.05em;
        color: #828282;

        span {
          text-decoration: line-through;
          min-width: 130px;
        }

        span.blue {
          display: block;
          font-weight: 700;
          text-decoration: none;
        }
      }
    }
  }

  .advantages-block {
    margin-top: 40px;

    &:first-child{
      margin-top: 0;
    }
  }

  .recommend-block {
    background: #FFFFFF;
    box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 0 10px 25px 15px;

    @media screen and (min-width: 640px) {
      &+.recommend-feature {
        margin-top: 40px;
      }
    }

    @media screen and (min-width: 830px) {
      &+.recommend-feature {
        margin-top: 70px;
      }
    }

    &__group {
      .recommend-block {
        margin-bottom: 30px;

        &:last-child {
          margin-bottom: 0;
        }

        @media screen and (min-width: 640px) {
          margin-bottom: 50px;

          &:last-child {
            margin-bottom: 0;
          }
        }

      }
    }


    &__title {
      margin-bottom: 10px;

      &.blue {
        color: #5271FF;
      }
    }

    &__caption {
      background: #CB6CE6;
      border-radius: 10px;
      padding: 8px 20px 6px;
      font-weight: 700;
      font-size: 13px;
      line-height: 150%;
      letter-spacing: 0.05em;
      color: #FFFFFF;
      margin-top: -10px;

      &+.recommend-block__check {
        padding-top: 20px;
      }



      &.violet {
        background: #8C52FF;
      }

      &.blue {
        background: #5271FF;
      }

      &.grey {
        background: #BDB8C8;
      }
    }

    &__text {
      margin-top: 20px;
      font-size: 13px;
      line-height: 150%;
      letter-spacing: 0.05em;
      width: 100%;

      @media screen and (min-width: 640px) {
        font-size: 16px;

        ul li::before {
          top: 12px;
        }

      }

      @media screen and (min-width: 1152px) {
        font-size: 20px;

        ul li::before {
          top: 15px;
        }

      }

      ul {
        li {
          padding: 6px 0 6px 20px;
          position: relative;

          &::before {
            content: "";
            position: absolute;
            top: 9px;
            left: 0;
            width: 6px;
            height: 6px;
            background: #8C52FF;
            border-radius: 4px;
          }
        }
      }

    }

    ul.list {
      font-size: 12px;
      line-height: 150%;
      letter-spacing: 0.05em;

      li {
        padding: 6px 0 6px 25px;
        position: relative;

        &::before {
          content: "";
          position: absolute;
          top: 8px;
          left: 0;
          width: 15px;
          height: 15px;
          background-repeat: no-repeat;
          background-size: 100%;
        }
      }

      @media screen and (min-width: 640px) {
        li{
          padding: 6px 0 6px 40px;
          &::before{
            width: 20px;
            height: 20px;
          }
        }
      }

      @media screen and (min-width: 1152px) {
        li::before{
          width: 24px;
          height: 24px;
        }
      }

    }

    &__advantages {
      margin-top: 15px;
      width: 100%;

      li::before {
        background-image: url("data:image/svg+xml,%3Csvg width='15' height='16' viewBox='0 0 15 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.9375 5.26504L5.225 0.977539C6.475 -0.272461 8.525 -0.272461 9.775 0.977539L14.0625 5.26504C15.3125 6.51504 15.3125 8.56504 14.0625 9.81504L9.775 14.1025C8.525 15.3525 6.475 15.3525 5.225 14.1025L0.9375 9.81504C-0.3125 8.56504 -0.3125 6.51504 0.9375 5.26504Z' fill='%23CB6CE6'/%3E%3Cpath d='M4.1875 8.16504C3.84375 8.16504 3.5625 7.88379 3.5625 7.54004C3.5625 7.19629 3.84375 6.91504 4.1875 6.91504H6.875V4.22754C6.875 3.88379 7.15625 3.60254 7.5 3.60254C7.84375 3.60254 8.125 3.88379 8.125 4.22754V6.91504H10.8125C11.1562 6.91504 11.4375 7.19629 11.4375 7.54004C11.4375 7.88379 11.1562 8.16504 10.8125 8.16504H8.125V10.8525C8.125 11.1963 7.84375 11.4775 7.5 11.4775C7.15625 11.4775 6.875 11.1963 6.875 10.8525V8.16504H4.1875Z' fill='white'/%3E%3C/svg%3E%0A");
      }
    }

    &__info {
      margin-top: 15px;
      width: 100%;

      li::before {
        background-image: url("data:image/svg+xml,%3Csvg width='15' height='15' viewBox='0 0 15 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.9375 5.225L5.225 0.9375C6.475 -0.3125 8.525 -0.3125 9.775 0.9375L14.0625 5.225C15.3125 6.475 15.3125 8.525 14.0625 9.775L9.775 14.0625C8.525 15.3125 6.475 15.3125 5.225 14.0625L0.9375 9.775C-0.3125 8.525 -0.3125 6.475 0.9375 5.225Z' fill='%238C52FF'/%3E%3Cpath d='M6.75 9.34154C6.75 9.34154 6.75 7.47769 6.75 3.75C6.75 3.75 7.25 3.75 8.25 3.75C8.25 3.75 8.25 5.61385 8.25 9.34154C8.25 9.34154 7.75 9.34154 6.75 9.34154ZM6.75 11.25C6.75 11.25 6.75 10.8171 6.75 9.95126C6.75 9.95126 7.25 9.95126 8.25 9.95126C8.25 9.95126 8.25 10.3842 8.25 11.25C8.25 11.25 7.75 11.25 6.75 11.25Z' fill='white'/%3E%3C/svg%3E%0A") !important;
      }
    }

    &__disadvantages {
      margin-top: 15px;
      width: 100%;

      li::before {
        background-image: url("data:image/svg+xml,%3Csvg width='15' height='16' viewBox='0 0 15 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.9375 5.26504L5.225 0.977539C6.475 -0.272461 8.525 -0.272461 9.775 0.977539L14.0625 5.26504C15.3125 6.51504 15.3125 8.56504 14.0625 9.81504L9.775 14.1025C8.525 15.3525 6.475 15.3525 5.225 14.1025L0.9375 9.81504C-0.3125 8.56504 -0.3125 6.51504 0.9375 5.26504Z' fill='%23BDB8C8'/%3E%3Cpath d='M4.1875 8.16504C3.84375 8.16504 3.5625 7.88379 3.5625 7.54004C3.5625 7.19629 3.84375 6.91504 4.1875 6.91504C9.2 6.91504 5.8 6.91504 10.8125 6.91504C11.1562 6.91504 11.4375 7.19629 11.4375 7.54004C11.4375 7.88379 11.1562 8.16504 10.8125 8.16504C5.8 8.16504 9.2 8.16504 4.1875 8.16504Z' fill='white'/%3E%3C/svg%3E%0A");
      }
    }

    &__rules {
      margin-top: 20px;
      width: 100%;

      li::before {
        background-image: url("data:image/svg+xml,%3Csvg width='15' height='16' viewBox='0 0 15 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0.9375 5.26504L5.225 0.977539C6.475 -0.272461 8.525 -0.272461 9.775 0.977539L14.0625 5.26504C15.3125 6.51504 15.3125 8.56504 14.0625 9.81504L9.775 14.1025C8.525 15.3525 6.475 15.3525 5.225 14.1025L0.9375 9.81504C-0.3125 8.56504 -0.3125 6.51504 0.9375 5.26504Z' fill='%23CB6CE6'/%3E%3Cpath d='M4.43672 8.30859C4.19297 8.06484 4.19297 7.67109 4.43672 7.42734C4.67422 7.18359 5.07422 7.18359 5.31797 7.42109L6.70547 8.81484L10.0992 5.42109C10.343 5.17734 10.7367 5.17734 10.9805 5.42109C11.2242 5.66484 11.2242 6.06484 10.9805 6.30859L7.14922 10.1398C6.90547 10.3773 6.51172 10.3773 6.26797 10.1398L4.43672 8.30859Z' fill='white'/%3E%3C/svg%3E%0A") !important;
      }
    }

    &__check {
      width: 100%;

      li::before {
