  .recommend-feature {
    margin-top: 30px;


    &:not(.fullwidth) {
      max-width: 980px;
      margin-left: auto;
      margin-right: auto;
    }

    &__items {
      width: 100%;
      overflow: visible;
      padding-top: 32px;

      &--flex {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;

        @media screen and (min-width: 480px) {
          margin: 0 -10px;

          .recommend-feature__item {
            width: calc(50% - 20px);
            margin: 25px 10px;
          }


        }

        @media screen and (min-width: 830px) {
          margin: 0 -10px;

          .recommend-feature__item {
            width: calc(33.33% - 20px);
            margin: 25px 10px;
          }


        }

        @media screen and (min-width: 960px) {
          margin: 0 -10px;

          .recommend-feature__item {
            width: calc(25% - 20px) !important;
            margin: 25px 10px !important;
          }


        }

        .recommend-feature__item {
          margin: 20px 0;
          width: 100%;

        }

      }

      .swiper-wrapper {
        overflow: visible;
      }

    }

    &__item {
      background: #FFFFFF;
      box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      position: relative;
      padding: 55px 30px 25px 10px;
      height: auto;
      display: flex;
      align-items: center;

      .icon {
        position: absolute;
        top: -15px;
        left: 10px;
        width: 60px;
        height: 60px;

        &.icon-right {
          left: auto;
          right: 10px;
        }

        svg {
          width: 100%;
          height: 100%;
        }
      }

      .text {
        font-size: 13px;
        line-height: 130%;
      }

    }

    @media screen and (min-width: 480px) {
      .recommend-block__title {
        text-align: center;
      }
    }

    @media screen and (min-width: 640px) {

      &__items {
        .swiper-wrapper {
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          margin: 0 -10px;
          width: calc(100% + 20px);
        }
      }

      &__item {
        width: calc(50% - 20px) !important;
        margin: 10px !important;
        padding: 55px 10px 20px;

        .text {
          font-size: 16px;
        }

      }
    }

    @media screen and (min-width: 830px) {
      margin-top: 40px;

      &__items {
        padding-top: 20px;
      }

      &__item {
        width: calc(33.33% - 20px) !important;
        padding: 65px 20px 20px;
        margin: 30px 10px !important;

        .icon {
          width: 80px;
          height: 80px;
          top: -25px;
        }

      }
    }

    @media screen and (min-width: 1152px) {
      &__item {
        .text {
          font-size: 20px;
        }
      }
    }

  }

  .how-much {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__table {
      background: #FFFFFF;
      box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      padding: 10px;
      margin-bottom: 30px;

      table {
        border-collapse: collapse;
      }

      tr td:first-child {
        font-weight: 700;
        font-size: 13px;
        line-height: 130%;
        letter-spacing: 0.05em;
        color: #8C52FF;
      }

      tr td {
        font-size: 13px;
        line-height: 150%;
        letter-spacing: 0.05em;
        padding: 9px;
        border: 1px solid #CCCCCC;

        &:first-child {
          border-left: none;
        }

        &:last-child {
          border-right: none;
        }
      }
    }
  }

