            margin-bottom: -9px;
          }

          .title {
            font-size: 20px;
            padding: 20px;
          }
        }

      }

    }

    &-items {
      display: flex;
      flex-direction: column;

      &--text {
        .compare-items__item {
          display: flex;
          flex-direction: column;
          margin: 15px 0;

          &:last-child {
            margin-bottom: 0;
          }

          .image {
            display: block;
            max-width: 210px;
            margin: 0 auto;
            margin-bottom: 10px;
          }

          .text {
            padding: 20px 10px;

            &-title {
              display: block;
              margin-bottom: 10px;
            }

          }

          @media screen and (min-width: 640px) {
            padding: 20px 180px 0 0;

            &:nth-child(even) {
              padding: 20px 0 0 180px;

              .image {
                left: 0;
                right: auto;
              }

            }

            .image {
              right: 0;
              left: auto;
            }
          }

          @media screen and (min-width: 830px) {
            padding: 20px 230px 0 0;
            margin: 20px 0;

            &:nth-child(even) {
              padding: 20px 0 0 230px;
            }

            .image {
              max-width: 250px;
              width: 250px;
              height: 250px;
            }
          }

          @media screen and (min-width: 1152px) {
            padding: 20px 460px 0 0;
            margin: 30px 0;
            min-height: 460px;

            &:nth-child(even) {
              padding: 20px 0 0 460px;
            }

            .image {
              max-width: 480px;
              width: 480px;
              height: 480px;
            }

            .text {
              padding: 20px;
            }

          }

        }
      }

      &__item {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        margin: 40px 0 0 0;

        &:first-child {
          margin-top: 0;
        }

        &.without-image {
          background: #FFFFFF;
          box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
          border-radius: 10px;

          .text {
            box-shadow: none;
          }

        }

        &:nth-child(even),
        &.even {
          align-items: flex-start;

          .title {
            margin-right: 0;
            margin-left: 10px;
            text-align: left;
          }


          @media screen and (min-width: 1152px) {
            .title {
              margin-right: 0;
              margin-left: -20px;
            }
          }


        }

        .image {
          img {
            border-radius: 10px;
          }
        }

        .title {
          background: #8C52FF;
          border-radius: 10px;
          font-weight: 700;
          font-size: 13px;
          line-height: 150%;
          letter-spacing: 0.05em;
          color: #FFFFFF;
          padding: 8px 12px;
          margin-bottom: -20px;
          position: relative;
          z-index: 1;
          margin-right: 10px;
          text-align: right;

          @media screen and (min-width: 1152px) {
            margin-right: -20px;
          }


          &.title-pink {
            background: #CA6CE7;
          }

          span.circle {
            position: relative;
            padding: 0 10px;

            &::after {
              content: "";
              position: absolute;
              top: 50%;
              left: 50%;
              width: 55px;
              height: 55px;
              background: rgba(203, 108, 230, 0.2);
              transform: translate(-50%, -50%);
              border-radius: 50%;
              z-index: -2;
            }

            &::before {
              content: "";
              position: absolute;
              top: 50%;
              left: 50%;
              width: 45px;
              height: 45px;
              background: #CB6CE6;
              transform: translate(-50%, -50%);
              border-radius: 50%;
              z-index: -1;
            }

            @media screen and (min-width: 830px) {
              &::after {
                width: 80px;
                height: 80px;
              }

              &::before {
                width: 65px;
                height: 65px;
              }
            }

          }

        }

        .text {
          background: #FFFFFF;
          box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
          border-radius: 10px;
          padding: 35px 10px 17px;
          font-size: 13px;
          line-height: 150%;
          letter-spacing: 0.05em;

          @media screen and (min-width: 640px) {
            font-size: 16px;

            ul li::before {
              top: 12px;
            }

          }

          @media screen and (min-width: 1152px) {
            font-size: 20px;

            ul li::before {
              top: 15px;
            }

          }

          ul {
            li {
              padding: 6px 0 6px 20px;
              position: relative;

              &::before {
                content: "";
                position: absolute;
                top: 9px;
                left: 0;
                width: 6px;
                height: 6px;
                background: #8C52FF;
                border-radius: 4px;
              }
            }
          }

          span.blue,
          span.pink {
            font-weight: 700;
          }
        }
      }
    }
  }

  .how-do {
    flex-direction: column-reverse;

    &__text {
      padding: 15px 10px 40px;
    }

    &__caption {
      margin: -20px 10px 0;
    }

    @media screen and (min-width: 640px) {
      &__caption {
        padding: 20px;
        font-size: 16px;
        max-width: 460px / 600px * 100%;
      }

      &__text {
        padding: 20px 20px 40px;
        font-size: 16px;
      }
    }

    @media screen and (min-width: 830px) {
      &__text {
        padding: 20px 20px 40px;
      }
    }

    @media screen and (min-width: 1152px) {
      display: flex;
      flex-direction: row-reverse;
      align-items: center;


      &.different {
        align-items: flex-start;
        .how-do__caption {
          max-width: 480px;
          margin: 20px 0 0 -20px;
        }

        .how-do__text {
          max-width: 700px;
          padding-right: 40px;
        }
      }

      &__caption {
        margin: 0 0 0 -20px;
        max-width: 550px;
        font-size: 20px;
      }

      &__text {
        padding: 20px 20px 20px 40px;
        max-width: 630px;
        font-size: 20px;
      }
    }

  }

  .usage-block {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    overflow: hidden;

    &__caption {
      background: #CB6CE6;
      border-radius: 10px;
      padding: 8px 17px 7px 8px;
      font-weight: 700;
      font-size: 13px;
      line-height: 150%;
      letter-spacing: 0.05em;
      color: #FFFFFF;
      margin-bottom: -50px;
      position: relative;
      z-index: 1;
      margin-left: 10px;
      margin-right: 10px;

      &.blue {
        background: #007aff;
      }

      &.grey {
        background: #BDB8C8;
      }
    }

    &__list {
      background: #FFFFFF;
      box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
      border-radius: 10px;
      padding: 65px 30px 20px 15px;
      width: 100%;

      .list {
        width: 100%;
