.detailCompare {
  padding: 0 0 70px;



  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 90px;

    &+.fleets-banner {
      padding-top: 30px;
    }

  }

  .container {
    position: relative;
  }

  &-figure {
    position: absolute;
    top: 100%;
    right: -30px;
    z-index: -1;
    width: 209px;
    height: 195px;
    transform: translateY(-35%);

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
      width: 255px;
      height: 237px;
    }

    @media screen and (min-width: 640px) {
      width: 305px;
      height: 283px;
      top: 75%;
      right: -20px;
    }

    @media screen and (min-width: 1152px) {

      &.bottom {
        top: 65%;
      }

      width: 402px;
      height: 392px;
      top: -40px;
      right: -160px;
      transform: none;
    }

  }

  .compare-items {

    &.left-to-right{
      .compare-items__item{
        @media screen and (min-width: 640px) {
          padding: 20px 180px 0 0;
          &:nth-child(even){
            padding: 20px 0 0 180px;
            .image{
              left: 0;
              right: auto;
            }
          }
          .image{
            left: auto;
            right: 0;
          }
        }
        @media screen and (min-width: 830px) {
          min-height: 360px;
          padding: 20px 340px 0 0;
          &:nth-child(even){
            padding: 20px 0 0 340px;
          }
            .image{
              width: 360px;
              height: 360px;
            }
        }
      }
    }



    &.with-images{
      .image {
        display: block;
        max-width: 200px;
        margin: 0 auto;
        padding-bottom: 10px;

        img {
          width: 100%;
          height: auto;
        }
      }

      .text{
        padding-top: 60px;
      }


    }

    &__item {
      .image {
        display: none;
        position: relative;

        &-dot {
          position: absolute;
          width: 38px;
          height: 38px;
          animation: pulse 2s ease 0s infinite normal forwards;

          svg {
            width: 100%;
            height: 100%;
          }
        }

        @keyframes pulse {
          0% {
            transform: scale(1);
          }

          50% {
            transform: scale(1.25);
          }

          100% {
            transform: scale(1);
          }
        }

      }

      &.without-caption {
        margin-right: 10px;
        margin-left: 10px;
        .text {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          padding-top: 17px;

          &-title{
            background: #8C52FF;
            border-radius: 8px;
            padding: 8px 15px;
            font-weight: 700;
            font-size: 13px;
            line-height: 150%;
            letter-spacing: 0.05em;
            color: #FFFFFF;
            margin-bottom: 14px;
            display: inline-block;
            margin-top: -7px;
            margin-left: -20px;
          }

        }

        .image {
          display: block;
          max-width: 200px;
          margin: 0 auto;
          margin-bottom: 10px;

          img {
            width: 100%;
            height: auto;
          }
        }


        &:nth-child(even){
          .text{
            align-items: flex-end;
          }
          .text-title{
            margin-left: 0;
            margin-right: -20px;
          }
        }

        @media screen and (min-width: 640px) {
          margin-right: 0;
          margin-left: 0;
          .text-title{
            font-size: 16px;
            margin-left: -30px;
          }

          &:nth-child(even){
            .text-title{
              margin-left: 0;
              margin-right: -30px;
            }
          }

        }

        @media screen and (min-width: 1152px) {
          .text-title{
            font-size: 20px;
            margin-left: -30px;
            padding: 13px 20px;
            margin-bottom: 20px;
          }

          &:nth-child(even){
            .text-title{
              margin-left: 0;
              margin-right: -30px;
            }
          }

        }

      }

      &.without-image {
        .title {
          margin-left: 10px;
        }

        &:nth-child(even) {
          .title {
            margin-left: -10px;
            margin-right: 10px;
          }
        }
      }

      &.fixed-image{
        .image{
          display: block;
          width: 260px;
          height: 260px;
          margin: 0 auto 10px;

          img{
            width: 100%;
            height: auto;
          }

        }
      }

      &.with-image{
        .image{
          width: 260px;
          height: 260px;
          display: block;
          margin: 0 auto 10px;

          img{
            width: 100%;
            height: auto;
          }

        }

        @media screen and (min-width: 1152px) {
          .image{
            width: 360px;
            height: 360px;
          }
        }

      }

      .text{
        p{
          margin: 10px 0;
        }
      }

    }

    /*&.left-to-right{
      margin-right: 10px;
      margin-left: 10px;
      .compare-items__item{
        align-items: flex-start;
        &:nth-child(even){
          align-items: flex-end;


          .title{
            margin-right: -10px;
            margin-left: 0;
          }

        }
      }
      .image{
        display: none;
      }
      .title{
        margin-bottom: -50px;
        margin-left: -10px;
        margin-right: 0;
      }

      .text{
        padding-top: 65px;
      }

    }*/

  }

  @media screen and (min-width: 480px) {
    .compare-items {
      &__item {

        &.without-image {
          .title {
            margin-left: 20px;
          }

          &:nth-child(even) {
            .title {
              margin-left: -10px;
              margin-right: 20px;
            }
          }
        }

      }
    }

  }

  @media screen and (min-width: 640px) {
    .compare-items {
      &__item {
        min-height: 220px;
        position: relative;
        padding: 120px 0 0 180px;

        &.without-image {
          padding: 20px 0 0 !important;
          min-height: 0 !important;
          margin: 60px 0 0;

          .title {
            margin-left: 260px / 1160px * 100%;
          }

          &:nth-child(even) {
            .title {
              margin-left: -10px;
              margin-right: 260px / 1160px * 100%;
            }
          }

        }

        &.without-caption {
          .image {
            max-width: 100%;
          }
        }

        &.fixed-image{
          padding: 20px 0 0 240px;
          min-height: 260px;

          &:nth-child(even){
            padding: 20px 240px 0 0;
          }

          .image{
            margin-bottom: 0;
          }

        }

        .image {
          display: block;
          position: absolute;
          top: 0;
          left: 0;
          z-index: 0;
          width: 220px;
          height: 220px;

          img {
            width: 100%;
            height: 100%;
          }
        }

        .title {
          z-index: 2;
          font-size: 16px;
        }

        .text {
          position: relative;
          z-index: 1;
          font-size: 16px;
          padding: 30px 20px;
        }

        &:nth-child(even) {
          padding: 120px 180px 0 0;

          .image {
            top: 0;
            right: 0;
            left: auto;
          }
        }
      }

      /*&.left-to-right{
        margin-right: 0;
        margin-left: 0;
        .compare-items__item{
          padding: 0 240px 0 0;
          &:nth-child(even){
            padding: 0 0 0 240px;

            .image{
              right: auto;
              left: 0;
            }

          }

          .image{
            display: block;
            left: auto;
            right: 0;
            width: 250px;
            height: 250px;
          }

        }

      }*/

    }
  }

  @media screen and (min-width: 960px) {
    .compare-items {
      &__item {
        min-height: 250px;
        padding: 0px 0 0 240px;

        .image {
          width: 250px;
          height: 250px;
        }

        &:nth-child(even) {
          padding: 0px 240px 0 0;
        }
      }
    }
  }

  @media screen and (min-width: 1152px) {
    .compare-items {
      &__item {
        min-height: 360px;
        padding: 40px 0 40px 340px;

        &.without-caption {
          .text {
            padding: 30px 20px;
          }
        }

        .image {
          width: 360px;
          height: 360px;
        }

        .title {
          font-size: 20px;
          margin-bottom: -75px;
          padding: 15px 20px;
        }

        .text {
          font-size: 20px;
          padding-top: 95px;
        }

        &:nth-child(even) {
          padding: 40px 340px 40px 0;
        }
      }
    }
  }

}