.detailUsage {
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 120px;
  }

  .container{
    position: relative;
  }

  &-figure{
    position: absolute;
    top: 50%;
    right: -20px;
    z-index: -1;
    width:180px;
    height: 152px;

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
      top: 100%;
      right: -25px;
      width: 226px;
      height: 252px;
      transform: translateY(-100%);
    }

    @media screen and (min-width: 640px) {
      width: 307px;
      height: 279px;
      top: 0;
      right: -40px;
      transform: translateY(-64px);
    }

    @media screen and (min-width: 1152px) {

      &.top{
        top: 0;
        transform: translateY(-50%);
      }

      width: 372px;
      height: 349px;
      top: 50%;
      transform: none;
      right: -60px;
    }

  }

  &-items {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__item {
      margin-bottom: 30px;
      width: 100%;

      &:last-child{
        margin-bottom: 0;
      }

      &:nth-child(even){
        .title{
          color: #5271FF;
        }

        .image-frame__dot svg path{
          fill: #5271FF;
        }

        .list-ul li::before{
          background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M1.5 8.36L8.36 1.5C10.36 -0.5 13.64 -0.5 15.64 1.5L22.5 8.36C24.5 10.36 24.5 13.64 22.5 15.64L15.64 22.5C13.64 24.5 10.36 24.5 8.36 22.5L1.5 15.64C-0.5 13.64 -0.5 10.36 1.5 8.36Z' fill='%235271FF'/%3E%3Cpath d='M7.09914 13.2299C6.70914 12.8399 6.70914 12.2099 7.09914 11.8199C7.47914 11.4299 8.11914 11.4299 8.50914 11.8099L10.7291 14.0399L16.1591 8.60988C16.5491 8.21988 17.1791 8.21988 17.5691 8.60988C17.9591 8.99988 17.9591 9.63988 17.5691 10.0299L11.4391 16.1599C11.0491 16.5399 10.4191 16.5399 10.0291 16.1599L7.09914 13.2299Z' fill='white'/%3E%3C/svg%3E%0A");
        }

      }

      .image-frame__dot svg path{
        fill: #8C52FF;
      }

      & > .title {
        max-width: 300px;
        margin: 0 auto 15px;
      }

      .title{
        font-size: 20px;
        font-weight: bold;
        color: #8C52FF;
      }

      .image {
        position: relative;
        max-width: 300px;
        margin: 0 auto;
        border-radius: 10px;
        overflow: hidden;
        margin-bottom: -10px;

        img {
          width: 100%;
          height: auto;
        }

        &-frame {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;

          &__dot{
            position: absolute;
            width: 25px;
            height: 25px;
            transition: all 0.25s ease;
            &.scale{
              transform-origin: center;
              transform: scale(2);
            }
            svg{
              width: 100%;
              height: 100%;
              object-fit: contain;
            }
          }
        }
      }

      .list{
        .title{
          display: none;
        }
      }

      .list-ul {
        background: #FFFFFF;
        box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15);
        border-radius: 10px;
        padding: 15px 20px;
        z-index: 1;
        position: relative;
        max-width: 280px;
        margin: 0 auto;

        @media screen and (min-width: 375px) {
          max-width: 360px;
        }

        @media screen and (min-width: 640px) {
          max-width: none;
        }


        li {
          padding: 6px 0 6px 25px;
          position: relative;
          font-size: 12px;
          line-height: 150%;
          letter-spacing: 0.05em;

          &:hover{
            text-decoration: underline;
          }

          &::before {
            content: "";
            position: absolute;
            top: 8px;
            left: 0;
            width: 15px;
            height: 15px;
            background-size: 100%;
            background-repeat: no-repeat;
            background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M1.5 8.36L8.36 1.5C10.36 -0.5 13.64 -0.5 15.64 1.5L22.5 8.36C24.5 10.36 24.5 13.64 22.5 15.64L15.64 22.5C13.64 24.5 10.36 24.5 8.36 22.5L1.5 15.64C-0.5 13.64 -0.5 10.36 1.5 8.36Z' fill='%238C52FF'/%3E%3Cpath d='M7.09914 13.2299C6.70914 12.8399 6.70914 12.2099 7.09914 11.8199C7.47914 11.4299 8.11914 11.4299 8.50914 11.8099L10.7291 14.0399L16.1591 8.60988C16.5491 8.21988 17.1791 8.21988 17.5691 8.60988C17.9591 8.99988 17.9591 9.63988 17.5691 10.0299L11.4391 16.1599C11.0491 16.5399 10.4191 16.5399 10.0291 16.1599L7.09914 13.2299Z' fill='white'/%3E%3C/svg%3E%0A");
          }
        }
      }
    }

    &__item.image-from-bottom{
      .title{
        margin: 0 10px 15px;
      }
      .image{
        margin-bottom: 0;
        margin-top: -10px;
      }
      .button{
        margin-top: 30px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        .btn{
          padding: 16px 18px;
          font-size: 13px;
          line-height: 17px;

          @media screen and (min-width: 480px) {
            padding: 15px;
          }

          @media screen and (min-width: 640px) {
            font-size: 16px;
            line-height: 21px;
            padding: 20px 17px;
          }

          @media screen and (min-width: 960px) {
            padding: 20px 27px;
          }

          @media screen and (min-width: 1152px) {
            font-size: 20px;
            line-height: 27px;
            padding: 23px 27px;
          }

        }

      }
    }

    &__item.left-to-right{

      .button{
        margin-top: 30px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        .btn{
          padding: 16px 18px;
          font-size: 13px;
          line-height: 17px;

          @media screen and (min-width: 480px) {
            padding: 15px;
          }

          @media screen and (min-width: 640px) {
            font-size: 16px;
            line-height: 21px;
            padding: 20px 17px;
          }

          @media screen and (min-width: 960px) {
            padding: 20px 27px;
          }

          @media screen and (min-width: 1152px) {
            font-size: 20px;
            line-height: 27px;
            padding: 23px 27px;
          }

        }

      }

      @media screen and (min-width: 640px) {
        flex-direction: column-reverse;

        .image{
          margin-top: 0;
          margin-bottom: -40px;
          margin-right: auto;
          margin-left: 0;
        }

        .list{
          margin-left: auto;
        }



      }

      @media screen and (min-width: 830px) {
        flex-direction: row-reverse;
        align-items: flex-start;
        justify-content: center;
        .image{
          width: 460px / 910px * 100%;
          margin-bottom: 0;
          margin-right: -40px;
        }
        .list{
          margin-top: 20px;
          width: 480px / 910px * 100%;
          max-width: 595px;
        }
      }

    }

    @media screen and (min-width: 640px) {
      &__item {
        display: flex;
        flex-direction: column;
        width: 100%;

        & > .title{
          display: none;
        }

        .image {
          order: 2;
          max-width: 540px;
          margin-top: -40px;
          margin-left: auto;
          margin-right: 0;
          margin-bottom: 0;
        }

        .list{
          order: 1;
          max-width: 420px;
          min-width: 230px;

          &-ul li{
            font-size: 16px;
            cursor: pointer;
            padding: 6px 0 6px 40px;

            &::before{
              width: 20px;
              height: 20px;
            }

          }

          .title{
            font-size: 26px;
            display: block;
            margin-bottom: 20px;
          }

        }

        .button{
          max-width: 420px;
          margin-left: auto;
        }

        &:nth-child(even){

          .image{
            margin-right: auto;
            margin-left: 0;
          }

          .list{
            margin-left: auto;

            .title{
              text-align: right;
            }

          }
        }


      }
    }

    @media screen and (min-width: 830px) {

      &__item {
        flex-direction: row;
        flex-wrap: wrap;
        align-items: flex-start;

        &.with-btn{
          .list{
            margin-bottom: auto;
          }
          .image{
            margin-bottom: -61px;
          }
          .button{
            width: 460px / 940px * 100%;
            max-width: 520px;
            margin-top: 0;
          }
        }

        .list {
          width: 460px / 940px * 100%;
          order: 2;
          max-width: 520px;
        }

        .button{
          width: 460px / 940px * 100%;
          order: 3;
          max-width: 520px;
        }

        .image {
          width: 460px / 910px * 100%;
          max-width: 660px;
          order: 3;
          margin-top: 0;
          margin-left: -20px;
        }

        &:nth-child(even){
          align-items: center;
          .image{
            order: 1;
            width: 400px / 910px * 100%;
            margin-right: 0;
            max-width: 460px;
          }
          .list{
            order: 2;
            width: auto;
            margin-right: auto;
            margin-left: -20px;
          }
        }

      }
    }

    @media screen and (min-width: 1152px) {

      &__item {
        margin-bottom: 60px;

        &.with-btn{
          .list{
            margin-bottom: 58px;
          }
          .image{
            margin-bottom: -73px;
          }
          .button{
            margin-top: 0;
          }
        }


        .list{
            &-ul li{
              font-size: 20px;
              padding-left: 40px !important;
              &::before{
                width: 24px !important;
                height: 24px !important;
              }
            }
          }

        &:nth-child(even){
          .image{
          }
        }

      }
    }

  }
}