.homeInterior{
  padding: 0 0 70px;

  @media screen and (min-width: 830px) {
    padding: 0 0 90px;
  }

  @media screen and (min-width: 1152px) {
    padding: 0 0 75px;
  }

  &.detailBlock {
    .section-h2 {
      @media screen and (min-width: 1152px) {
        text-align: center;
      }
    }
  }

  .container{
    position: relative;
  }

  &-figure{
    position: absolute;
    top: 100%;
    left: -20px;
    z-index: -1;
    width: 176px;
    height: 185px;
    transform: translateY(-60%);

    img{
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
      width: 180px;
      height: 184px;
      left: -25px;
      transform: translateY(-60%);
    }

    @media screen and (min-width: 640px) {
      width: 297px;
      height: 311px;
      top: 100%;
      left: auto;
      right: -25px;
      transform: translateY(-55%);
    }

    @media screen and (min-width: 1290px) {
      width: 368px;
      height: 429px;
      right: -160px;
      transform: translateY(-65%);
    }

  }

  .section-h2{
    margin-bottom: 30px;
  }

  &-slider{
    padding-bottom: 45px;
    max-width: 440px;
    margin: 0 auto;
    &::after{

      @media screen and (min-width: 640px) {
        display: none;
      }

      content: "";
      display: block;
      position: absolute;
      bottom: 0;
      right: 0;
      width: 30px;
      height: 30px;
      background-repeat: no-repeat;
