    }
    &__slide{
      height: auto;
      border-radius: 10px;
      overflow: hidden;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  @media screen and(min-width: 640px) {
    &-slider:not(.swiper-initialized){
      width: calc(100% + 10px);
      max-width: calc(100% + 10px);
      margin: 0 -5px;

      .swiper-wrapper{
        display: flex;
        flex-wrap: wrap;
      }
      .homeInterior-slider__slide{
        width: calc(33.33% - 10px);
        margin: 5px;
        &.fullwidth{
          width: calc(66.66% - 10px);
        }
      }
    }
  }

  @media screen and(min-width: 960px) {
    &-slider:not(.swiper-initialized){
      width: calc(100% + 20px);
      max-width: calc(100% + 20px);
      margin: 0 -10px;
      .homeInterior-slider__slide{
        width: calc(33.33% - 20px);
        margin: 10px;
        &.fullwidth{
          width: calc(66.66% - 20px);
        }
      }
    }
  }

  @media screen and(min-width: 1290px) {
    &-slider:not(.swiper-initialized){
      .homeInterior-slider__slide{
        width: 460px / 1200px * 100%;
        margin: 10px;
        flex-grow: 1;
        &.fullwidth{
          width: 660px / 1200px * 100%;
        }
      }
    }
  }

}